
html, body {
    margin: 0;

    padding: 0;
    
    will-change: scroll-position;
}

* {
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
}

*:after, *:before {
    -webkit-box-sizing: inherit;
    -moz-box-sizing: inherit;
    box-sizing: inherit;
}
:root {
    --P_PageSection__dragged_height: 40px;
    --P_PageSection_sorting-transition-duration: 0.35s;
    --P_PageSection_sorting-transition-easing: ease-out;
    
    --P_PageSection--placeholder_text: "Um√≠stit sekci zde...";
}


.P_PageSection--placeholder {
    position: relative;

    min-height: 0px;
    height: var(--P_PageSection__dragged_height);
    width: 100%;

    color: #888;
    outline: 2px dashed currentColor;
    outline-offset: -4px;
    
    transition-property: height, min-height;
    transition-duration: 0.1s;
    transition-timing-function: var(--P_PageSection_sorting-transition-easing);
    
    will-change: height;
}

.P_PageSection--placeholder:after {
    content: var(--P_PageSection--placeholder_text);

    font-size: 17px;

    position: absolute;
    top: 0px;
    left: 0px;

    width: 100%;
    height: 100%;

    display: flex;
    justify-content: center;
    align-items: center;
}

.P_PageSection--placeholder__transitions {
    height: var(--P_PageSection__dragged_height);
    width: 100%;

    transition-property: height;
    transition-duration: var(--P_PageSection_sorting-transition-duration);
    transition-timing-function: var(--P_PageSection_sorting-transition-easing);

    will-change: height;
}


.E_NewPageSectionSelector {
    z-index: 110;
    position: fixed;
    top: calc(-100% + 20px);
    left: 0px;
    
    padding: 20px;
    width: 300px;
    height: 100%;
    
    background-color: #ddd;
    
    transition: all 0.3s ease-in-out 0s;
}

[class*='E_NewPageSection'] {
    will-change: transform, opacity, width, height;
}

.E_NewPageSectionSelector:hover {
    top: 0%;
}

.E_NewPageSectionSelector--page-section-types {
    display: flex;
    flex-wrap: wrap;
    align-items: flex-start;
    justify-content: space-between;
}

.E_NewPageSectionSelector--page-section-type {
    font-size: 23px;

    margin-bottom: 20px;

	width: calc(50% - 10px);
    height: 105px;    
    
    color: #fff;
    background-color: #404040;
    
    display: flex;
    justify-content: center;
    align-items: center;
}

.E_NewPageSectionSelector--clone {
    z-index: 120;
}
.P_PageSection {
    position: relative;

    transition-property: height;
    transition-duration: var(--P_PageSection_sorting-transition-duration);
    transition-timing-function: var(--P_PageSection_sorting-transition-easing);
}

.P_PageSection__placed {
    z-index: 100;

    overflow: hidden;
}

.P_PageSection__dragged {
    z-index: 100;

    height: var(--P_PageSection__dragged_height);
    min-height: var(--P_PageSection__dragged_height);
    width: 100%;

    overflow: hidden;
}

.E [class*='P_PageSection'], .E [class*='P_PageSection']:after {
    will-change: transform, opacity, width, height;
}

.E .P_PageSection:after {
    content: attr(data-section-name);

    font-size: 19px;

    z-index: 100;
    position: absolute;
    top: 0px;
    left: 0px;

    width: 100%;
    height: 100%;

    color: #fff;
    background-color: #404040;
    opacity: 0;
    visibility: hidden;

    display: flex;
    justify-content: center;
    align-items: center;

    transition-property: opacity, visibility;
    transition-duration: var(--P_PageSection_sorting-transition-duration);
    transition-timing-function: var(--P_PageSection_sorting-transition-easing);
}

.E .P_PageSection__dragged:after {
    opacity: 1;
    visibility: visible;
}
